import { Component, h, Host, State } from '@stencil/core';
import { LimelSelectCustomEvent, Option } from '@limetech/lime-elements';
import { chartItems } from './chart-items-bar';

/**
 * Scatter chart
 * A scatter plot uses Cartesian coordinates to display values for typically two variables.
 *
 * It's good for:
 * - Exploring relationships or correlations between two variables.
 * - Identifying clusters, trends, or outliers in a dataset.
 *
 * :::tip
 * **Use:**
 * - When examining potential correlations, such as between height and weight.
 * - For larger datasets where individual points reveal distribution patterns.
 *
 * **Avoid:**
 * - For data with only a few values (line or bar charts may be more effective).
 * - When comparing exact values (scatter plots are more exploratory).
 * :::
 * @sourceFile chart-items-bar.ts
 */
@Component({
    tag: 'limel-example-chart-type-scatter',
    shadow: true,
    styleUrl: 'chart-examples.scss',
})
export class ChartTypeScatterExample {
    @State()
    private maxValue = 100;

    @State()
    private orientation: 'landscape' | 'portrait' = 'landscape';

    private orientations: Option[] = [
        { text: 'landscape', value: 'landscape' },
        { text: 'portrait', value: 'portrait' },
    ];

    public render() {
        const defaultMaxValue = `${this.maxValue}`;

        return (
            <Host class="large">
                <h4>Subscriptions per month</h4>
                <limel-chart
                    type="scatter"
                    items={chartItems}
                    orientation={this.orientation}
                    maxValue={this.maxValue}
                />
                <limel-example-controls>
                    <limel-select
                        label="orientation"
                        value={this.getSelectedOrientation()}
                        options={this.orientations}
                        onChange={this.handleOrientationChange}
                    />
                    <limel-input-field
                        type="number"
                        label="maxValue"
                        value={defaultMaxValue}
                        onChange={this.handleMaxValueChange}
                    />
                </limel-example-controls>
            </Host>
        );
    }

    private getSelectedOrientation() {
        return this.orientations.find(
            (option) => option.value === this.orientation,
        );
    }

    private handleOrientationChange = (
        event: LimelSelectCustomEvent<Option<string>>,
    ) => {
        this.orientation = event.detail.value as 'landscape' | 'portrait';
    };
    private handleMaxValueChange = (event) => {
        this.maxValue = +event.detail;
    };
}
